export default {
	data() {
		return {
			id: "",
			info: {}
		}
	},
	onLoad(option) {
		this.id = option.id;
	},
	onShow() {
		global.$http.post('/dining/info/getInfo', {
			params: {
				dining_id: this.id
			},
		}).then(res => {
			if (res.status === "0") {
				this.info = res.data;
			} else {
				uni.showToast({
					title: res.msg,
					icon: 'none'
				});
			}
		}).catch(err => {
			uni.showToast({
				title: err.message,
				icon: 'none'
			});
		});
	},
	methods: {
		toEdit: function(e){
			uni.navigateTo({
				url: '../work/saveFood?para=' + encodeURIComponent(JSON.stringify(this.info))
			});
		}
	}
}
